import React, { Component, Suspense } from 'react';
import Select from 'react-select';
import { Accordion, OverlayTrigger, Tooltip } from 'react-bootstrap';
// import { baseUrl } from '../../../../Assets/AssetsHelper';
import PerfectScrollbar from 'react-perfect-scrollbar'
import { Calendar } from 'react-date-range';
import { PopUpOption, ColumnOption, HideOlderOption } from './Utils'
const ColorPicker = React.lazy(() => import('../../../CommonComponents/ColorPicker'));
const Background = React.lazy(() => import('./Elements/Background'));
const CustomCss = React.lazy(() => import('./Elements/CustomCss'));
const OnSiteUploads = React.lazy(() => import('./Elements/OnSiteUpload'));
const DesignCardStyle = React.lazy(() => import('./Elements/CardStyle'));
const Advanced = React.lazy(() => import('./Elements/Advanced'));
class HorizontalColumnTemplate extends Component {
    state = {
        background: { status: false, color: "#fff", image: null, transparent: 0, backgroundImageId: 0 },
        cardStyle: { cardColor: "#000000", borderRadius: 10, fontFamily: null, fontVarient: null, fontSize: 10, textColor: "#fff", authorColor: "#fff", socialIconColor: "#fff", },
        advanced: { time: false, author: false, hideContent: false },


        onSiteUpload: { bgColor: "#fff", borderRadius: 0, fontFamily: null, fontVarient: null, fontSize: 10, textColor: "#fff", buttonText: "Scan QR or Tap here to upload UGC", buttonStatus: false, qrCodeStatus: false },


        themeSettings: { suffleAssets: false, showmore: false, autoLoad: false, trimContent: false, bestFit: false, hideOldAssets: { status: false, range: null }, postPopUp: null, popUpUrl: null, numberOfPosts: 20, columns: 0, postPadding: 15, hideTextTypeAssets: false, minPostWitdh: 300, playFullVideo: false, hideUrls: false, timePerSlide: 0, scrollSpeed: 0, popUpduration: 0 },


        customCss: { cssStatus: false, css: "" },

    }



    onClickEnableBackground = event => {
        const { background } = this.state;
        background.status = event.target.checked
        if (!event.target.checked) background.transparent = 1
        else background.transparent = 0
        this.setState({ background })
    }
    onClickBackgroundColor = (color) => {
        const { background } = this.state;
        background.color = color
        this.setState({ background })
    }
    onClickTouploadImage = (value, backgroundImageId, callBack) => {
        const { background } = this.state;
        background.image = value
        background.backgroundImageId = backgroundImageId
        this.setState({ background }, () => callBack())
    }

    backgroundImageRemove = event => {
        const { background } = this.state;
        background.image = null
        background.backgroundImageId = 0
        this.setState({ background })
    }




    onClickEnableTime = event => {
        const { advanced } = this.state;
        advanced.time = event.target.checked
        this.setState({ advanced })
    }
    onClickEnableAuthor = event => {
        const { advanced } = this.state;
        advanced.author = event.target.checked
        this.setState({ advanced })
    }
    onClickEnableHideContent = event => {
        const { advanced } = this.state;
        advanced.hideContent = event.target.checked
        this.setState({ advanced })
    }


    onClickEnableCustomCss = event => {
        const { customCss } = this.state;
        customCss.cssStatus = event.target.checked
        this.setState({ customCss })
    }

    onChangeCss = event => {
        const { customCss } = this.state;
        customCss.css = event.target.value
        this.setState({ customCss })
    }



    onClickEnableSuffleAssets = event => {
        const { themeSettings } = this.state;
        themeSettings.suffleAssets = event.target.checked
        this.setState({ themeSettings })
    }
    onClickShowMore = event => {
        const { themeSettings } = this.state;
        themeSettings.showmore = event.target.checked
        this.setState({ themeSettings })
    }
    onClickAutoLoad = event => {
        const { themeSettings } = this.state;
        themeSettings.autoLoad = event.target.checked
        this.setState({ themeSettings })
    }
    onClickTrimContent = event => {
        const { themeSettings } = this.state;
        themeSettings.trimContent = event.target.checked
        this.setState({ themeSettings })
    }

    onClickBestFit = event => {
        const { themeSettings } = this.state;
        themeSettings.bestFit = event.target.checked
        this.setState({ themeSettings })
    }
    onClickHideOldAssets = event => {
        const { themeSettings } = this.state;
        themeSettings.hideOldAssets.status = event.target.checked
        this.setState({ themeSettings })
    }
    onNumberOfPosts = event => {
        const { themeSettings } = this.state;
        themeSettings.numberOfPosts = event.target.value
        this.setState({ themeSettings })
    }
    onPostPadding = event => {
        const { themeSettings } = this.state;
        themeSettings.postPadding = event.target.value
        this.setState({ themeSettings })
    }
    onSelectPostPopUp = event => {
        const { themeSettings } = this.state;
        themeSettings.postPopUp = event
        this.setState({ themeSettings })
    }

    onPopUpUrl = event => {
        const { themeSettings } = this.state;
        themeSettings.popUpUrl = event.target.value
        this.setState({ themeSettings })
    }


    onChangeCardColor = (cardColor) => {
        const { cardStyle } = this.state;
        cardStyle.cardColor = cardColor
        this.setState({ cardStyle })
    }
    onCardBorderRadius = event => {
        const { cardStyle } = this.state;
        cardStyle.borderRadius = event.target.value
        this.setState({ cardStyle })
    }
    onCardFontFamily = event => {
        const { cardStyle } = this.state;
        cardStyle.fontFamily = event.target.value
        this.setState({ cardStyle })
    }
    onCardFontVarient = event => {
        const { cardStyle } = this.state;
        cardStyle.fontVarient = event.target.value
        this.setState({ cardStyle })
    }
    onCardFontSize = event => {
        const { cardStyle } = this.state;
        cardStyle.fontSize = event.target.value
        this.setState({ cardStyle })
    }
    onCardTextColor = (value) => {
        const { cardStyle } = this.state;
        cardStyle.textColor = value
        this.setState({ cardStyle })
    }
    onCardAuthorColor = (value) => {
        const { cardStyle } = this.state;
        cardStyle.authorColor = value
        this.setState({ cardStyle })
    }
    onCardSocialColor = (value) => {
        const { cardStyle } = this.state;
        cardStyle.socialIconColor = value
        this.setState({ cardStyle })
    }


    onOnSiteBgColor = (value) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.bgColor = value
        this.setState({ onSiteUpload })
    }
    onOnSiteBorderRadius = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.borderRadius = event.target.value
        this.setState({ onSiteUpload })
    }


    onOnSiteFontFamily = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.fontFamily = event.target.value
        this.setState({ onSiteUpload })
    }
    onOnSiteFontVarient = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.fontVarient = event.target.value
        this.setState({ onSiteUpload })
    }
    onOnSiteFontSize = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.fontSize = event.target.value
        this.setState({ onSiteUpload })
    }

    onOnSiteTextColor = (value) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.textColor = value
        this.setState({ onSiteUpload })
    }
    onOnSiteButtonText = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.buttonText = event.target.value
        this.setState({ onSiteUpload })
    }

    onOnSiteButtonStatus = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.buttonStatus = event.target.checked
        this.setState({ onSiteUpload })
    }
    onOnSiteQrCodeStatus = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.qrCodeStatus = event.target.checked
        this.setState({ onSiteUpload })
    }


    componentDidUpdate(prevProps, prevState) {
        if (prevState != this.state) this.props.tempFormData(this.state)
    }

    render() {
        const { selectedThemeData } = this.props
        const { background, cardStyle, advanced, onSiteUpload, buttonStatus, themeSettings, customCss, backgroundModal } = this.state;

        return <Accordion defaultActiveKey="1">
            <Background onClickEnableBackground={this.onClickEnableBackground} onClickBackgroundColor={this.onClickBackgroundColor} background={background} onClickTouploadImage={this.onClickTouploadImage} backgroundImageRemove={this.backgroundImageRemove} />
            <DesignCardStyle cardStyle={cardStyle} onChangeCardColor={this.onChangeCardColor} onCardBorderRadius={this.onCardBorderRadius} onCardFontSize={this.onCardFontSize} />

            <Advanced advanced={advanced} onClickEnableTime={this.onClickEnableTime} onClickEnableAuthor={this.onClickEnableAuthor} onClickEnableHideContent={this.onClickEnableHideContent} />
            <OnSiteUploads onSiteUpload={onSiteUpload} onOnSiteBgColor={this.onOnSiteBgColor} onOnSiteBorderRadius={this.onOnSiteBorderRadius} onOnSiteFontSize={this.onOnSiteFontSize} onOnSiteTextColor={this.onOnSiteTextColor} onOnSiteButtonText={this.onOnSiteButtonText} onOnSiteButtonStatus={this.onOnSiteButtonStatus} onOnSiteQrCodeStatus={this.onOnSiteQrCodeStatus} />
            <Accordion.Item eventKey="6" className="pb-3">
                <Accordion.Header className="w-100">
                    <span className="fs-7 fw-semibold">Theme Settings</span>
                </Accordion.Header>
                <Accordion.Body className="border rounded-1 mt-1 p-2">
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-8 text-gray-800 d-flex">Suffle Assets
                            <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Shuffles your assets in random order irrespective of date/time.</Tooltip>}>
                                <span className="svg-icon svg-icon-6 ms-1">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-1-11v6h2v-6h-2zm0-4v2h2V7h-2z" fill="currentColor" />
                                    </svg>
                                </span>
                            </OverlayTrigger>
                        </span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.suffleAssets} onClick={this.onClickEnableSuffleAssets} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-8 text-gray-800 d-flex">Auto Load
                            <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Auto Load button?.</Tooltip>}>
                                <span className="svg-icon svg-icon-6 ms-1">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-1-11v6h2v-6h-2zm0-4v2h2V7h-2z" fill="currentColor" />
                                    </svg>
                                </span>
                            </OverlayTrigger>
                        </span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.autoLoad} onClick={this.onClickAutoLoad} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-8 text-gray-800 d-flex">Trim Content
                            <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">It will trim assets text after a limit.</Tooltip>}>
                                <span className="svg-icon svg-icon-6 ms-1">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-1-11v6h2v-6h-2zm0-4v2h2V7h-2z" fill="currentColor" />
                                    </svg>
                                </span>
                            </OverlayTrigger>
                        </span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.trimContent} onClick={this.onClickTrimContent} />
                    </label>
                    <div className="border-bottom">
                        <label className="form-check form-switch py-2 align-items-center justify-content-between h-40px py-1">
                            <span className="form-check-label ps-0 fs-8 text-gray-800 d-flex">Hide Old Assets
                                <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Hides old assets on your live wall.</Tooltip>}>
                                    <span className="svg-icon svg-icon-6 ms-1">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                            <path fill="none" d="M0 0h24v24H0z" />
                                            <path d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-1-11v6h2v-6h-2zm0-4v2h2V7h-2z" fill="currentColor" />
                                        </svg>
                                    </span>
                                </OverlayTrigger>
                            </span>
                            <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.hideOldAssets.status} onClick={this.onClickHideOldAssets} />
                        </label>
                        <div className="w-100 mb-2">
                            <select className="form-control form-control-sm w-100 fw-semibold">
                                <option defaultValue>One Day Ago</option>
                                <option value="1">Two Days Ago</option>
                                <option value="2">Three Days Ago</option>
                                <option value="3">Four Days Ago</option>
                                <option value="4">Five Days Ago</option>
                                <option value="5">Six Days Ago</option>
                                <option value="6">Seven Days Ago</option>
                                <option value="7">One Month</option>
                                <option value="8">Custom Date</option>
                            </select>
                        </div>
                        <div className="w-100 mb-2">
                            <Calendar
                                date={new Date()}
                                onChange={this.handleSelect}
                                color="#313A53"
                            />
                        </div>
                    </div>

                    <div className="border-bottom">
                        <label className="form-check form-switch py-2 align-items-center justify-content-between h-40px py-1">
                            <span className="form-check-label ps-0 fs-8 text-gray-800 d-flex">On Click
                                <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Choose action on click</Tooltip>}>
                                    <span className="svg-icon svg-icon-6 ms-1">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                            <path fill="none" d="M0 0h24v24H0z" />
                                            <path d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-1-11v6h2v-6h-2zm0-4v2h2V7h-2z" fill="currentColor" />
                                        </svg>
                                    </span>
                                </OverlayTrigger>
                            </span>
                        </label>
                        <div className="w-100 mb-2">
                            <Select className="form-control form-control-sm w-100 fw-semibold" value={themeSettings.postPopUp} options={PopUpOption} onChange={this.onSelectPostPopUp} />
                        </div>
                        {
                            themeSettings.postPopUp && Object.keys(themeSettings.postPopUp).length > 0 && themeSettings.postPopUp.value == 3 ? <div className="w-100 mb-2">
                                <input type="url" name="onClickLink" className="form-control form-control-sm" placeholder="Enter url..." value={themeSettings.popUpUrl} onChange={this.onPopUpUrl} />
                            </div> : null
                        }

                    </div>
                    <div className="border-bottom">
                        <label className="form-check form-switch py-2 align-items-center justify-content-between h-40px py-1">
                            <span className="form-check-label ps-0 fs-8 text-gray-800 d-flex">Total Number of Assets to Display
                                <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Maximum no. of assets to be displayed on live wall.</Tooltip>}>
                                    <span className="svg-icon svg-icon-6 ms-1">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                            <path fill="none" d="M0 0h24v24H0z" />
                                            <path d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-1-11v6h2v-6h-2zm0-4v2h2V7h-2z" fill="currentColor" />
                                        </svg>
                                    </span>
                                </OverlayTrigger>
                            </span>
                        </label>
                        <div className="w-100 mb-2">
                            <input type="number" name="assetsCount" className="form-control form-control-sm" placeholder="" value={themeSettings.numberOfPosts} defaultValue={themeSettings.numberOfPosts} onChange={this.onNumberOfPosts} />
                        </div>
                    </div>
                    <div className="">
                        <label className="form-check form-switch py-2 align-items-center justify-content-between h-40px py-1">
                            <span className="form-check-label ps-0 fs-8 text-gray-800 d-flex">Columns
                                <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Choose action on click</Tooltip>}>
                                    <span className="svg-icon svg-icon-6 ms-1">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                            <path fill="none" d="M0 0h24v24H0z" />
                                            <path d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-1-11v6h2v-6h-2zm0-4v2h2V7h-2z" fill="currentColor" />
                                        </svg>
                                    </span>
                                </OverlayTrigger>
                            </span>
                        </label>
                        <div className="w-100 mb-2">
                            <select className="form-control form-control-sm w-100 fw-semibold">
                                <option defaultValue>Responsive</option>
                                <option value="1">1 Columns</option>
                                <option value="2">2 Columns</option>
                                <option value="3">3 Columns</option>
                                <option value="3">4 Columns</option>
                                <option value="3">5 Columns</option>
                            </select>
                        </div>
                    </div>
                </Accordion.Body>
            </Accordion.Item>
            <CustomCss customCss={customCss} onClickEnableCustomCss={this.onClickEnableCustomCss} onChangeCss={this.onChangeCss} />
        </Accordion>
    }
}

export default HorizontalColumnTemplate;